.filter-modal {
    z-index: 999;
    top: 30px;
    right: 0;
    background-color: $white;
    border-radius: 5px;
    box-shadow: $modalsShadow;
    padding: 0 10px 10px 10px;
    cursor: auto;
    max-height: 400px;
    section {
        button {
            padding: 5px;
            border-radius: 0;
        }
    }
    >* {
        max-height: 100%;
        width: 100px;
        margin: 0 10px;
        .filter-list {
            button {
                padding: 5px;
                border-radius: 10px;
            }
            .remove-filter-btn {
                background-color: $buttonMainHover;
            }
        }
        h3 {
            color: $mainBlack;
            margin: 10px 0;
        }
        button {
            padding: 5px 10px;
            width: 100%;
            background-color: $buttonMainBg;
            color: $colorIconsNav;
            margin-bottom: 5px;
        }
        button:hover {
            background-color: $buttonMainHover;
        }
    }
}