.updates-header {
    height: 30vh;
    border-bottom: 1px solid rgba(109, 109, 109, 0.35);
    padding-top: 15px;
    padding-left: 15px;


    svg {
        color: $buttonMainBg;

        &:hover {

            color: $buttonMainHover;
        }
    }

    h1 {
        margin-top: 10px;
        margin-bottom: 20px;
        margin-left: 15px;
    }

    .updates-header-options {
        width: 100%;
        margin-left: 10px;


        textarea {
            width: 90%;
            border-radius: 10px;
            resize: none;
            height: 75px;
            padding: 5px;

            &:focus {
                border-color: $buttonMainBg;
            }
        }

        .updates-btns {
            padding: 0 5px;
            width: 90%;
        }

        .image-uploader {
            margin-right: 10px;

            label {

                >* {
                    margin-top: 4px;
                    border-radius: 5px;
                }

                svg {
                    align-self: center;
                    width: 30px;
                    height: 30px;
                    border-radius: 5px;
                    font-size: 25px;
                    color: $colorBgBoardBars;
                    background-color: $buttonMainBg;
                    cursor: pointer;
                    transition: .3s;

                    &:hover {
                        background-color: $buttonMainHover;
                    }
                }
            }

            .image-uploaded {
                margin-left: 5px;
                padding: 5px;
                border-radius: 5px;
                height: 30px;
                margin-top: 4px;
                color: #1cdc1c;

                svg {
                    background-color: inherit;
                    cursor: auto;
                    color: #1cdc1c;
                }
            }
        }


        .ldio-subadnemdcd div {
            left: 47px;
            top: 25.5px;
            position: absolute;
            animation: ldio-subadnemdcd linear 1s infinite;
            background: #ffffff;
            width: 6px;
            height: 13px;
            border-radius: 5px;
            transform-origin: 3px 24.5px;
        }

        .ldio-subadnemdcd div:nth-child(1) {
            transform: rotate(0deg);
            animation-delay: -0.9s;
            background: #ffffff;
        }

        .ldio-subadnemdcd div:nth-child(2) {
            transform: rotate(36deg);
            animation-delay: -0.8s;
            background: #ffffff;
        }

        .ldio-subadnemdcd div:nth-child(3) {
            transform: rotate(72deg);
            animation-delay: -0.7s;
            background: #ffffff;
        }

        .ldio-subadnemdcd div:nth-child(4) {
            transform: rotate(108deg);
            animation-delay: -0.6s;
            background: #ffffff;
        }

        .ldio-subadnemdcd div:nth-child(5) {
            transform: rotate(144deg);
            animation-delay: -0.5s;
            background: #ffffff;
        }

        .ldio-subadnemdcd div:nth-child(6) {
            transform: rotate(180deg);
            animation-delay: -0.4s;
            background: #ffffff;
        }

        .ldio-subadnemdcd div:nth-child(7) {
            transform: rotate(216deg);
            animation-delay: -0.3s;
            background: #ffffff;
        }

        .ldio-subadnemdcd div:nth-child(8) {
            transform: rotate(252deg);
            animation-delay: -0.2s;
            background: #ffffff;
        }

        .ldio-subadnemdcd div:nth-child(9) {
            transform: rotate(288deg);
            animation-delay: -0.1s;
            background: #ffffff;
        }

        .ldio-subadnemdcd div:nth-child(10) {
            transform: rotate(324deg);
            animation-delay: 0s;
            background: #ffffff;
        }

        .loadingio-spinner-spinner-gvjl1rpqs7q {
            width: 30px;
            height: 30px;
            display: inline-block;
            overflow: hidden;
            background: $buttonMainBg;
        }

        .ldio-subadnemdcd {
            width: 100%;
            height: 100%;
            position: relative;
            transform: translateZ(0) scale(0.3);
            backface-visibility: hidden;
            transform-origin: 0 0;
            /* see note above */
        }

        .ldio-subadnemdcd div {
            box-sizing: content-box;
        }

        button {
            height: 30px;
            margin-top: 5px;
            display: flex;
            align-items: center;
            border-radius: 5px;
        }
    }



}

.updates-container {

    overflow: overlay;
    padding: 0 10px;
    height: 70vh;
    background-color: $colorBgBoardBars;

    .update-options-modal {

        background-color: #ffffff;
        position: fixed;
        z-index: 999;
        width: 180px;
        border-radius: 5px;
        display: flex;
        flex-direction: column;
        box-shadow: 0px 3px 10px -5px #484444;
        padding: 10px;
        left:0;

        p:not(:first-child) {
            margin-top: 15px;
        }

        p {
            transition: .3s;

            &:hover {
                background-color: rgba(218, 218, 218, 0.287);
            }
        }

    }

    img {
        max-height: 200px;
        max-width: fit-content;
        object-fit: fill;
    }


    .update-box {
        width: 93%;
        padding: 10px;
        box-shadow: 0px 3px 10px -5px #484444;
        margin: 10px;
        background-color: $white;
        border-radius: 5px;
        overflow: hidden;

        .update-options-modal {
            align-self: flex-end;
        }

        svg {
            align-self: flex-end;
        }

        .remove-update-btn {
            padding: 0;
            background-color: inherit;
            color: #7e7e7e;
            position: absolute;
            right: 7px;
            top: 3px;
            font-size: 1.2rem;

            &:hover {
                color: #4a4747;
            }
        }

        .update-box-header {
            border-bottom: 1px solid rgba(109, 109, 109, 0.35);
            padding-bottom: 10px;
            margin-bottom: 10px;
            width: 100%;

            .member-name {
                font-weight: bold;
            }

            img {
                margin-top: 0;
                width: 35px;
                height: 35px;
                border-radius: 50%;
                margin-right: 10px;
            }
        }

        .update-box-main {
            min-height: 100px;
            width: 100%;

            .update-text {
                font-weight: initial;
                display: inline;
                word-break: break-all;
            }

        }


        .update-box-edit {
            width: 100%;

            textarea {
                resize: none;
                border-radius: 10px;
                width: 93%;
                height: 60px;
                padding: 5px;
            }
        }

        .submit-edit-btn {
            align-self: flex-end;
            top: 17px;
        }

        .edit-update-btn {
            margin: 5px 0;
            color: #7e7e7e;
            width: fit-content;

            &:hover {
                text-decoration: underline;
                cursor: pointer;
                color: #4a4747
            }
        }

        .update-box-footer {
            width: 100%;
        }

        .replies-box {

            .reply-box {
                background-color: #e1e1e1;
                padding: 15px;
                max-width: fit-content;
                border-radius: 8px;
                margin: 5px 0;
                overflow: hidden;

                &:hover {
                    .options-reply-btn {
                        transition: .3s;
                        transform: translate(0, 1px);

                        &:hover {
                            color: $buttonMainHover;
                        }

                    }
                }


            }

            .options-reply-btn {
                transform: translate(0, -15px);
                transition: .3s;
                align-self: flex-end;
                position: absolute;
                top: 0;

                @media (max-width:450px){
                    transform: translate(0, 0);
                }
            }


            img {
                width: 30px;
                height: 30px;
                border-radius: 50%;
                margin-right: 5px;
            }

            .reply-name {
                font-weight: bold;
            }

            .reply-text {
                margin-left: 15px;
                word-break: break-all;
                align-self: flex-end;
            }

            .update-reply-btn {
                padding: 0;
                align-self: flex-end;
            }

            textarea {
                resize: none;
                border-radius: 7px;
                margin-top: 6px;
                width: 100%;
                margin-left: 5px;
                height: 24px;
            }

        }

        .reply-footer {
            border: 1px solid rgba(109, 109, 109, 0.35);
            border-radius: 5px;
            overflow: hidden;
            position: relative;

            textarea {
                border: 0;
                width: 360px;
                resize: none;
                height: 35px;
                padding: 5px;
            }


        }


    }

}

.columns-container{
    padding-left:5px;
    img{
        width:30px;
        height:30px;
        border-radius: 50%;
    }

    input{
        width:100%;
        text-align: center;
    }

    >*{
        display:flex;
        height:40px;
        width:90%;
        align-items: center;

        >*{
            margin-left:5px;
            width: 100px;
        }
    }
    .user-img-container {
        position: relative;

        img {
            cursor: pointer;
            border-radius: 50%;
            width: 30px;
            height: 30px;

        }

        .member-img-container {

            >:not(:first-child).member-img {
                margin-left: -15px;
            }
        }

        .no-members-icon-plus {
            font-family: monospace;
            font-size: 15px;
            width: 17px;
            height: 17px;
            position: absolute;
            background-color: #0085ff;
            color: #f2f1dfb0;
            border-radius: 50%;
            text-align: center;
            right: -5px;
            font-weight: bold;
            bottom: 10px;
            cursor: pointer;
        }

        .no-members-container {
            position: relative;

            .no-members-icon {
                height: 30px;
                width: 30px;
            }

        }

        .member-letter {
            height: 100%;
            width: 50%;
            margin: 0 auto;
            font-size: 2rem;
            border-radius: 50%;
            background-color: $colorIconsTasks;
            color: $colorTask;
            padding-top: 6px;
            cursor: pointer;
            width: 30px;
            height: 30px;
        }

        .task-number-of-imgs {
            font-family: monospace;
            font-size: 15px;
            width: 17px;
            height: 17px;
            position: absolute;
            background-color: #0085ff;
            color: #f2f1dfb0;
            border-radius: 50%;
            text-align: center;
            right: 42px;
            font-weight: bold;
            bottom: 16px;
            cursor: pointer;
        }
    }

    .users-modal {
        background-color: $white;
        z-index: 999;
        border-radius: 5px;
        top: 35px;
        left: 0;
        padding: 0 10px;
        box-shadow: $modalsShadow;
        width: max-content;


        .task-users-box {
            border-bottom: 1px solid #a8a8a87d;
            cursor: auto;

            svg {
                font-size: 1.5rem;
                color: $buttonMainBg;
            }

            .member-name {
                margin-left: 10px;
            }
        }

        h3 {
            margin: 15px 0;

        }

        .user-box {
            margin-bottom: 10px;
        }

        .user-box-info {
            p {
                text-align: left;
                cursor: pointer;
            }
        }

        .board-users-box {
            cursor: auto;

            .member-name {
                margin-left: 10px;
            }

            svg {
                font-size: 1.5rem;
                color: $buttonMainBg;
            }
        }
    }

    .label-container {
        height: 100%;


        .label-box {
            width: 100%;
            text-align: center;
            border-radius: 1px;
            cursor: default;
            font-size: 0.8rem;
            height: 100%;
        }

        .task-label-name {
            height: 100%;
            width: 100%;
            cursor: pointer;

            &:hover {
                transition: 0.3s;
                box-shadow: $brightBoxShadow;
            }
        }


        .label-list {
            width: 110px;
            background-color: $white;
            box-shadow: $modalsShadow;
            z-index: 999;
            border-radius: 1px;
            left: 0;
            top:30px;
            word-break: break-all;
            padding: 5px 5px 0 5px;

            .tag-add-container{
                width:100%;
            }


            >*:hover {
                transition: 0.3s;
                box-shadow: $brightBoxShadow;
            }

            .label-selector {
                width: 100%;
                text-align: center;
                height: 40px;
                margin-bottom: 5px;
                border-radius: 1px;
                cursor: pointer;
            }
        }

        .stuck {
            background-color: #e44258;
        }

        .working {
            background-color: #fdab3d;
        }

        .done {
            background-color: #00ca72;
        }

        .low {
            background-color: #7bb0ae;
        }

        .medium {
            background-color: #a028c7;
        }

        .high {
            background-color: #e82d2d;
        }

        .stuck,
        .working,
        .done,
        .low,
        .medium,
        .high {
            color: #f1f1f1;
        }
    }

    .react-datepicker__input-container {

        input {

            &:hover,
            &:focus {
                border: $dottedBorder;
            }
        }

    }

    .tags {
        height: 100%;
        padding-left: 5px;

        .task-label-name {
            overflow: overlay;
        }

        .tag-remove-icon {
            font-size: 1.2rem;
            transform: rotate(180deg);
        }
    }
    
}

.modal-screen-wrapper{
    z-index: 998;
}

.updates-image-modal {
    animation: scale-in-ver-top .3s;
    padding: 25px;
    box-shadow: $modalsShadow;
    background-color: $white;
    top: 5vh;
    left: 5vw;
    position: absolute;
    z-index: 999;

    img {
        box-shadow: $modalsShadow;
        height: 600px;
        width: 548px;

    }
}