.board {
    display: flex;

    .board-container {
        width: 100%;
        overflow: hidden;


        .groups-container {
            height: 80vh;
            overflow-y: overlay;
            background-color: $colorBgBoardBars;
        }
    }

    @media (max-width:450px) {

        .group-add-btn {
            position: fixed;
            bottom: 10px;
            right: 15px;
            font-size: 3rem;
            background: white;
            border-radius: 50%;
            color: $buttonMainBg;
            box-shadow: $modalsShadow;
        }

        .board-container {

            height: 92vh;
        }
    }

}